import Link from "next/link";

interface PaginationProps {
    items: number;
    pageSize: number;
    currentPage: number;
    pathName: string;
}

export default function PaginationComponent({ items, pageSize, currentPage, pathName }: PaginationProps) {
    const pagesCount = Math.ceil(items / pageSize);
    const pages = Array.from({ length: pagesCount }, (_, i) => i + 1);
    return (
        <div className="col-span-3 flex flex-row justify-center gap-2">
            {
                pages.map((page) => (
                    <Link key={page} scroll={false} className={`font-dm-sans font-bold border-2 border-partial px-2 rounded-sm ${page == currentPage ? 'bg-primary text-white' : 'bg-[#FFFBF2] text-primary'}`}
                        href={{ pathname: pathName, query: { page: page } }}>
                        {page}
                    </Link>
                ))
            }
        </div>
    )
}
